import React, { useState } from 'react';
import ReactDiffViewer, { DiffMethod } from "react-diff-viewer";
const oldJSON = require("./data/new.json");
const newJSON = require("./data/new.json");

const App = () => {
  const [devUrl, setDevUrl] = useState('');
  const [prodUrl, setProdUrl] = useState('');
  const [devResponse, setDevResponse] = useState(null);
  const [prodResponse, setProdResponse] = useState(null);
  const [devButtonColor, setDevButtonColor] = useState('btn-primary'); // Initial color is blue
  const [prodButtonColor, setProdButtonColor] = useState('btn-primary'); // Initial color is blue
  const [useThingProxy, setUseThingProxy] = useState(false); // State to track checkbox status
  const [loading, setLoading] = useState(false); // Track loading state for fetching data
  const [devFetched, setDevFetched] = useState(false); // Track if Dev URL has been fetched successfully
  

  const handleCheckboxChange = () => {
    setUseThingProxy(!useThingProxy); // Toggle checkbox state
  };

  const Spinner = () => (
    <div class="spinner-border spinner-border-sm" role="status">
    <span class="visually-hidden">Loading...</span>
  </div>
  );

  


  const handleDevUrlChange = (e) => {
    setDevButtonColor('btn-primary');
    setDevFetched(false); // Reset devFetched state when URL changes
    setDevUrl(e.target.value);
  };

  const handleProdUrlChange = (e) => {
    setProdButtonColor('btn-primary');
    setProdUrl(e.target.value);
  };

  const fetchData = async (url, setResponse, id) => {
    try {
      // Prefix URL if checkbox is checked
      const finalUrl = useThingProxy ? `https://thingproxy.freeboard.io/fetch/${url}` : url;
      setLoading(true); // Start loading
      const response = await fetch(finalUrl);
      if (!response.ok) {
        throw new Error('Network response was not ok');
      }
      const data = await response.json();
      setResponse(data);
      // Set button color to green if response status is 200
      if (id === "dev") { 
        setDevFetched(true); // Set devFetched state to true after successful fetch
        setDevButtonColor('btn-success'); }
      if (id === "prod") { setProdButtonColor('btn-success'); }

    } catch (error) {
      console.error('Error fetching data:', error.message);
      //setDevButtonColor('btn-danger'); // Set button color to red if there's an error fetching data
      if (id === "dev") { setDevButtonColor('btn-danger'); }
      if (id === "prod") { setProdButtonColor('btn-danger'); }
    }
    finally {
      setLoading(false); // Stop loading
    }
  };

  const compareResponses = () => {
    if (devResponse && prodResponse) {
      const areIdentical = JSON.stringify(devResponse) === JSON.stringify(prodResponse);
      alert(areIdentical ? 'API responses are identical' : 'API responses are different');
      setDevResponse("");
      setProdResponse("");
      setDevButtonColor('btn-primary'); // Reset button color to blue after comparing responses
      setProdButtonColor('btn-primary'); // Reset button color to blue after comparing responses
    } else {
      alert('Please fetch data from both URLs first');
      setDevButtonColor('btn-primary'); // Reset button color to blue after comparing responses
      setProdButtonColor('btn-primary'); // Reset button color to blue after comparing responses
    }
  };

  const newStyles = {
    variables: {
      light: {
        codeFoldGutterBackground: "#6F767E",
        codeFoldBackground: "#E2E4E5"
      }
    }
  };

  return (<><br />
    <div className='container'>
      <h1>Compare API Responses</h1>
      <p>The comparison of two api responses to determine if they are identical is limited to this page.</p><br>
      </br>

      <div class="form-check form-switch">
        <input class="form-check-input" type="checkbox" id="useThingProxy" checked={useThingProxy} onChange={handleCheckboxChange} />
        <label class="form-check-label" for="flexSwitchCheckDefault">CORS API</label>
      </div>
      {loading && <Spinner />} {/* Conditionally render spinner */}
      <br />
      <div>
        <div className="mb-10">
          <label htmlFor="devUrl" className="form-label">First API URL:</label>
          <input className="form-control" type="text" id="devUrl" value={devUrl} onChange={handleDevUrlChange} />
          <br></br>
          <button type="button" className={`btn ${devButtonColor} btn-sm`} onClick={() => fetchData(devUrl, setDevResponse, "dev")}>Fetch Data</button>
          
              
        </div>
        <br />
        <div className="mb-10">
          <label htmlFor="prodUrl" className="form-label">Second API URL:</label>
          <input className="form-control" type="text" id="prodUrl" value={prodUrl} onChange={handleProdUrlChange} /><br></br>
          <button type="button" className={`btn ${prodButtonColor} btn-sm`} onClick={() => fetchData(prodUrl, setProdResponse, "prod")}>Fetch Data</button>
        </div>
        <br>
        </br>
      </div>
      <br />

      <div class="d-flex justify-content-center">

    <ReactDiffViewer
        oldValue={JSON.stringify(devResponse, undefined, 4)}
        newValue={JSON.stringify(prodResponse, undefined, 4)}
        splitView={true}
        compareMethod={DiffMethod.WORDS}
        styles={newStyles}
        leftTitle="First API"
        rightTitle="Second API"
      />



    </div>


    </div>
    


    
  </>
  );
};

export default App;
